
.tab-list {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
}
.tab-item {
  -ms-flex-positive: 1;
      flex-grow: 1;
  text-align: center;
  position: relative;
}


/*一像素边框*/
.tab-item::after {
  content: '';
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
}
@media (min-device-pixel-ratio:2) {
.tab-item::after {
    transform: translateY(0.5);
}
}
@media (min-device-pixel-ratio:3) {
.tab-item::after {
    transform: translateY(0.33);
}
}
.tab-item a {
  display: block;
  font-size: 14px;
  text-decoration: none;
  color: rgb(77, 85, 93);
  line-height: 40px;
}
.tab-item a.active {
  color: rgb(240, 20, 20)
}



