}
}
.star-24 .star-item{
  width: 10px;
  height: 10px;
  margin-right: 3px;
  background-size: cover;
}
.star-24:last-child{
  margin-right: 0;
}
.star-24 .on{
  background-image: url('star24_on@2x.png');
}
.star-24 .half{
